
import { onMounted, ref } from '@vue/runtime-core';
import { Ranking } from '@/models/ranking';
import axios from 'axios';


export default {
    name: "Rankings",
    setup() {
        const rankings = ref<Ranking[]>([]);
        
        onMounted(async () => {
            const {data} = await axios.get<Ranking[]>('rankings');

            rankings.value = data;
        });

        return {
            rankings,
        }
    }
}
