
import {ref, SetupContext} from "vue"
import axios from 'axios'


export default {
    name: "Products",
    props: ['products', 'filters', 'lastPage'],
    emits: ['set-filters'],
    setup(props: any, context: SetupContext) {
        const selected = ref<number[]>([]);
        const link = ref('');
        const error = ref('');

        const search = (s: string) => {
          context.emit('set-filters', {
            ...props.filters,
            s,
            page: 1
          });
        }

        const sort = (sort: string) => {
          context.emit('set-filters', {
            ...props.filters,
            sort,
            page: 1
          });
        }

        const loadMore = () => {
          context.emit('set-filters', {
            ...props.filters,
            page: props.filters.page + 1
          });
        }

        const select = (id: number) => {
          if (selected.value.some(s => s === id)) {
            selected.value = selected.value.filter(s => s !== id)
            return;
          }

          selected.value = [...selected.value, id];
        }

        const generate = async () => {
          try {
            const {data} = await axios.post('links', {
              products: selected.value
            });
  
            link.value = `Link generated: https://ecommerce.adrianstudio.es/${data.code}`;
          } catch (e) {
            error.value = 'Necesitas estar logueado para generar el link'
          } finally {
            setTimeout(() => {
              link.value = '';
              error.value = '';
            }, 5000);
          }
        }

        return {
          search,
          link,
          sort,
          error,
          loadMore,
          selected,
          select,
          generate
        }
    }
}
